___CSS_LOADER_EXPORT___.push([module.id, `body {
	margin: 0;
	background-image: radial-gradient(red 5%, orange 15%, brown 60%);
	position: relative;
}

.tabs {
	display: flex;
	flex-direction: column;
	align-items: center;
	height: 100vh;
	background-image: radial-gradient(red 5%, orange 15%, brown 60%);
}

ul {
	list-style-type: none;
	padding: 0;
	display: flex;
	gap: 125px;
	margin: 30px 0 -2px 0;
}

li {
	width: 120px;
	height: 100px;
	background-image: brown;
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: x-large;
	border: 1px solid black;
	border-bottom: none;
	border-radius: 5px 5px 0 0;
	cursor: pointer;
}

nav {
	display: flex;
	justify-content: center;
}

.selected-tab {
	background-image: radial-gradient(orange 15%, brown 60%);
}

p {
	white-space: pre;
	text-align: center;
}
